import React from 'react';
import Spinner from '../spinner';
import $ from 'jquery';
import Item from '../item';

const pagination = 10;

class ShowContent  extends React.Component{

    constructor(props){
        super(props);
        this.jsonData = [];

        /**
         * ES5isMounted()
         * componentWillUnmountfalse
         * ajax
         * isMounted, 
         */
        this.isUnMount = false;

        this.state = {
            newStories: [],
            isLoading: true,
            isLoadingMore: false
        };
    }

    hideLoader() {
        this.setState({
            isLoading: false
        });
    }

    componentDidMount() {
        let sourceUrl = 'https://hacker-news.firebaseio.com/v0/showstories.json';
        $.get(sourceUrl, function (response) {

            /** getContentJson()hideLoadersetState,*/
            if(this.isUnMount){
                return;
            }

            if (response && response.length == 0) {
                this.hideLoader();
                return;
            }

            this.jsonData = response;

            /** */
            let startIndex = 0;
            let endIndex = startIndex + pagination;
            this.getContentJson(startIndex, endIndex, false);
        }.bind(this));
    }


    componentWillUnmount(){
        this.isUnMount = true;
        $(window).unbind('scroll');
    }

    /**
     * showstoriesjson[13553224, 13552376, 13558813, 13557421, 13543072, 13550176 ...]
     * @param startIndex     
     * @param endIndex     
     * @param isLoadingMore  sign
     */
    getContentJson(startIndex, endIndex, isLoadingMore) {

        for(let i = startIndex; i <= endIndex; i++) {

            if (i == endIndex){
                /** 100*/
                this.hideLoader();

                /** isLoadingMorefalse*/
                if (isLoadingMore) {
                    this.setState({ isLoadingMore: false });
                }

                /** */
                this.loadMore(endIndex);
                return false;
            }
            /** iid*/
            this.getContentData(this.jsonData[i],i);
        }
    }

    /**
     * id
     * @param id id
     * @param idIndex id
     */
    getContentData(id, idIndex) {
        /** 10*/
        if(idIndex > this.jsonData.length-1){
            return false;
        }

        let contentUrl = 'https://hacker-news.firebaseio.com/v0/item/' + id + '.json';
        $.get(contentUrl, function (response) {

            /**
             * 10106310
             * json
             * json, mark
             * 
             */
            // if (response.length == 0) {
            //     this.hideLoader();
            //     return;
            // }

            /** ajaxsetState*/
            if(this.isUnMount){
                return;
            }


            let domain = response.url ? response.url.split(':')[1].split('//')[1].split('/')[0] : '';
            response.domain = domain;
            this.setState({newStories : this.state.newStories.concat(response)});
        }.bind(this));
    }

    /** 
     * $(window).scrollTop() 
     * $(document).height() : 
     * $(window).height()  780px
     *
     *  $(window).scrollTop() =  $(document).height() - $(window).height()
     *  $(document).height() = $(window).height() +  $(window).scrollTop()
     */
    loadMore(startIndex) {
        $(window).unbind('scroll');

        /** jsonscroll*/
        if(startIndex > this.jsonData.length-1){
            return false;
        }
        $(window).bind('scroll', function () {

            if ($(window).scrollTop() == $(document).height() - $(window).height()) {
                let endIndex = startIndex + pagination;
                this.setState({isLoadingMore : true}); //To show loader at the bottom
                this.getContentJson(startIndex, endIndex, true);
            }
        }.bind(this));
    }

    /**
     * 
     */
    getNewStories(){
       return this.state.newStories.map((response, index) =>
                <div key={index}>
                    <Item response={response}/>
                </div>
            );
    }


    render() {
        return (
            <div className="content-container">
                <div className={this.state.isLoading ? '': 'hide'}>
                    <Spinner />
                </div>

                {this.getNewStories()}

                <div className={ this.state.isLoadingMore ? 'mtop50' : 'hide'}>
                    <Spinner />
                </div>
            </div>
        )
    }

}

module.exports = ShowContent;


